//hero
.hero {
	height: calc(100vh - 120px);
	max-height: 82rem;
	@media(max-width: 992px) {
		height: calc(100vh - 80px);
		
	}
	
	&__slider-title {
		margin-top: 9rem;
		margin-bottom: 1.5rem;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		@media(max-width: 992px) {
			margin-top: 7rem;
		}
		
	}
	&__slider-subtitle {
		margin-bottom: 6.4rem;
		max-height: 19.5rem;
		overflow: hidden;
		@media(max-width: 992px) {
			margin-bottom: 5rem;
		}
		
	}
}
section.hero{
	min-height: 70rem;
	@media(max-width: 768px) {
		min-height: 50rem;
	}
}
//title
.general-title {
	font-size: 12rem;
	line-height: 1;
	font-family: $ff2;
	font-weight: normal;
	color: #000000;
	text-transform: uppercase;
	@media(max-width: 992px) {
		font-size: 8rem;
		color: lighten(#000000, 10%);
	}
	@media(max-width: 768px) {
		font-size: 6rem;
	}
}

//subtitle
.subtitle {
	display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-align: left;
	font-size: 4rem;
	line-height: 1.6;
	color: #000000;
	font-weight: normal;
	text-transform: uppercase;
	letter-spacing: -1px;
	@media(max-width: 992px) {
		color: lighten(#000000, 10%);
	}
	@media(max-width: 768px) {
		font-size: 3rem;
	}
}

//slider
.slider-container {
	position: relative;
	width: 100%;
	height: 100%;
	max-width: 178.5rem;
	margin: 0 auto;
	padding: 0 1.5rem;
	padding-top: 40px;
	@media(max-width: 1200px) {
		padding-right: 0;
	}
	@media(max-width: 992px) {
		padding-top: 0;
		padding-left: 0;
	}
	.swiper-slide {
		display: flex;
		height: 100%;
	}
}

.full-slider {
	height: 100%;
	min-width: 0;
	&__item {
		position: relative;
		display: flex;
		width: 100%;
		height: 100%;

	}
	&__col {
		flex: 0 1 50%;
		overflow: hidden;
	}
	&__col--info {
		position: relative;
		padding-bottom: 8rem;
		padding-right: 2rem;
		z-index: 1;
		@media(max-width: 992px) {
			flex-basis: 100%;
			padding-left: 2rem;
			padding-bottom: 6rem;
		}
	}
	&__col--img {
		background-color: #F7FAFB;
		@media(max-width: 992px) {
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
		}
		&::after {
			@media(max-width: 992px) {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				background-color: rgba(255, 255, 255, 0.45);
			}
		}
	}
	&__nav {
		position: absolute;
		bottom: 2rem;
		left: 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 16rem;
		z-index: 100;
		@media(max-width: 992px) {
			width: 100%;
			justify-content: center;
			bottom: 3rem;
		}
	}

}
.slider-img {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

//slider-arrow
.slider-button-prev,
.slider-button-next {
	display: flex;
	align-items: center;
	flex-grow: 1;
	width: 2.1rem;
	height: 2.1rem;
	padding: 2rem 0;
	cursor: pointer;
	@media(max-width: 992px) {
		display: none;
	}
	&:hover {
		.icon *{
			fill: lighten(#000000, 20%);
		}
	}
}
.slider-button-next {
	justify-content: flex-end;
}

//slider-pagination
.slider-pagination {
	.swiper-pagination-bullet {
		width: 3rem;
		border-radius: 0.3rem;
		background-color: #ffffff;
		opacity: 0.5;
	}
	.swiper-pagination-bullet-active {
		background-color: #D1D94D;
		opacity: 1;
	}
	.swiper-pagination-current {
		color: #000000;
	}
}
.slider-pagination.swiper-pagination-bullets {
	@media(max-width: 992px) {
		width: auto;
	}
}
//fraction
.slider-pagination.swiper-pagination-fraction {
	text-align: center;
	font-size: 1.7rem;
	line-height: 1;
	color: rgba(0,0,0,.5);
	@media(max-width: 992px) {
		display: none;
	}
}
//slide-bar
.slider-pagination-bar {
	position: relative;
	width: 4.9rem;
	height: 0.2rem;
	overflow: hidden;
	top: 4px;
	@media(max-width: 992px) {
		display: none;
	}
	&__line {
		width: 0;
		height: 100%;
		background-color: #D1D94D;
	}
}


.icon {
	&--small {
		width: 2.1rem;
		height: 2.1rem;
	}
	
}
.icon *{
	fill: #000000;
	transition: fill 0.3s;
}

