@mixin center($pos: absolute, $vert: true, $horiz: true) {
	position: $pos;

	@if ($horiz) {
		left: 50%;
	}

	@if ($vert) {
		top: 50%;
	}

	@if ($horiz == false) {
		transform: translateY(-50%);
	}

 @else if ($vert == false) {
		transform: translateX(-50%);
	}

 @else {
		transform: translate(-50%, -50%);
	}
}
