*,*::after,*::before {
	box-sizing: border-box;
}

html {
	font-size: 62.5%;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

body {
	position: relative;
	min-width: 32rem;
	margin: 0;
	padding: 0;
	font-size: 1.4rem;
	font-family: $ff;
}

h1, h2, h3, h4, h5, h6, ul, p {
	margin: 0;
	padding: 0;
}

ul {
	list-style-type: none;
}

input {
	-webkit-appearance: none;
	-moz-appearance: textfield;
}

button,
input,
textarea {
	font-family: inherit;

	margin: 0;
	padding: 0;

	border: none;
	background: none;

	&:focus {
		outline: 0;
	}
}

.container {
	width: 100%;
	max-width: 120rem;
	margin: 0 auto;
	padding: 0 1.5rem;
}

.overlay {
	position: fixed;
	display: none;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background-color: rgba(0,0,0,.7);
	z-index: 2;
	&.active {
		display: block;
	}
}

.lock {
	overflow: hidden;
}


