.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	font-size: 1.4rem;
	padding: 2.3rem 3.5rem;
	text-transform: uppercase;
	color: #fff;
	text-decoration: none;
	background-color: #BEC644;
	cursor: pointer;
	transition: background-color 0.2s;
	&:hover {
		background-color: darken(#BEC644, 7%);
	}
	&__icon-right {
		margin-left: 5px;
	}
}
